<template>
  <Transition name="content-id__intro" mode="out-in">
    <div class="content-id__intro" v-if="currentMovie">
      <img
        :src="imgUrlFull + currentMovie.backdrop_path"
        alt=""
        class="content-id__intro-poster"
      />
      <div class="content-id__intro-block">
        <div class="content-id__intro-left">
          <h1 class="content-id__intro-left-title">
            {{ currentMovie.title || currentMovie.name }}
          </h1>
          <p class="content-id__intro-left-overview">
            {{ currentMovie.overview }}
          </p>
          <p class="content-id__intro-left-date">
            <span style="margin-right: 5px">
              {{
                new Date(currentMovie.release_date).getFullYear() ||
                new Date(currentMovie.first_air_date).getFullYear()
              }},
            </span>
            <span v-for="(genres, idx) in currentMovie.genres" :key="genres.id">
              {{ genres.name
              }}<span
                v-if="idx < currentMovie.genres.length - 1"
                style="margin-right: 5px"
                >,</span
              >
            </span>
            <span class="content-id__intro-left-date_runtime">
              {{ currentMovie.runtime ? runtime : "" }}
            </span>
          </p>
          <button class="content-id__intro-left-btn">
            <i class="fas fa-play"></i>
            <span>Смотерть трейлер</span>
          </button>
        </div>
        <div class="content-id__intro-right">
          <img v-lazy="imgUrl + currentMovie.poster_path" alt="" />
        </div>
        <div class="content-id__intro-actors">
          <h2 class="content-id__intro-actors-title">В главных ролях</h2>
          <div class="content-id__intro-actors-cast">
            <Actors :count="6" :type="type" :id="currentMovie.id" />
          </div>
        </div>
      </div>
    </div>
    <Loader v-else />
  </Transition>
</template>

<script setup>
import { computed } from "vue";
import { imgUrlFull, imgUrl } from "@/static";
import Actors from "@/components/UI/Actors.vue";
const props = defineProps(["type", "currentMovie"]);

const runtime = computed(() => {
  let min = props.currentMovie.runtime % 60;
  let hour = Math.floor(props.currentMovie.runtime / 60);

  return hour ? `${hour}ч ${min}м` : `${min}м`;
});
</script>
