<template>
  <section class="content-id">
    <IntroId :type="type" :currentMovie="getContentId" />
    <BudgetId :currentMovie="getContentId" />
  </section>
</template>

<script setup>
import IntroId from "./IntroId.vue";
import BudgetId from "./BudgetId.vue";
import { onMounted, computed } from "vue";
import { useInfoId } from "@/stores/infoId";
import { useRoute } from "vue-router";

const route = useRoute();
const infoId = useInfoId();
const props = defineProps(["type"]);

const getContentId = computed(() =>
  props.type == "movie" ? infoId.movie : infoId.tv
);

onMounted(() => {
  setTimeout(() => {
    infoId.getForId(props.type, route.params.id);
  }, 500);
});
</script>
